.art01 {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;

    .numbTitle {
        width: 30%;
        height: 100%;
        display: flex;
        flex-direction: column;
        padding: 40px;

        img {
            margin: 100px 0;
            width: 90px;
            height: auto;
        }

        p {
            font-family: 'CodeNext';
            font-size: 45px;
            line-height: 50px;
        }
    }

    .imgs {
        width: 30%;
        height: 100%; 
        position: relative;

        .img1 {
            position: absolute;
            height: 100%;
            width: auto;
        }

        .img2 {
            position: absolute;
            top: 100px;
            right: -150px;
            height: auto;
            width: 400px;
        }
    }

    .text {
        width: 40%;
        height: 100%;
        display: flex;

        p {
            margin-left: 100px;
            padding: 150px;
        }
    }
}

.art02 {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;

    .numbTitle {
        width: 30%;
        height: 100%;
        display: flex;
        flex-direction: column-reverse;
        padding: 40px;

        img {
            margin: 50px 50px;
            width: 90px;
            height: auto;
            align-self: flex-end;
        }

        p {
            font-family: 'CodeNext';
            font-size: 45px;
            line-height: 50px;
        }
    }

    .imgs {
        width: 30%;
        height: 100%; 
        position: relative;

        .img1 {
            position: absolute;
            height: 100%;
            width: auto;
        }

        .img2 {
            position: absolute;
            top: 50px;
            right: -350px;
            height: auto;
            width: 500px;
        }
    }

    .text {
        width: 40%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 100px;
        padding: 50px;
    }
}

